#customAlert-overlay,
#customConfirm-overlay {
  display: none;
  opacity: 0.5;
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  background: #000;
}
.customalert {
  background-color: #fff;
  font-family: arial;
  border-radius: 5px;
  width: 550px;
  font-size: 1em;
  display: none;
  position: fixed;
  max-width: 100vw;
  max-height: 100vh;
  z-index: 1000;
  top: 5%;
  left: 50%;
  padding: 10px;
  box-shadow: 0 0 10px 0 rgba(0,0,0,0.3);
  -webkit-box-shadow: 0 0 10px 0 rgba(0,0,0,0.3);
  margin-left: -275px;
}
@media (min-width: 321px) {
  .customalert {
    top: 25%;
  }
}
@media (max-width: 550px) {
  .customalert {
    margin-left: -50vw;
  }
}
.customalert .customalert_header {
  background: #fff;
  color: #232323;
  text-align: center;
  font-size: 0.75em;
}
.customalert .customalert_body {
  padding: 10px;
  color: #232323;
  font-size: 1.2em;
  background: #fff;
  text-align: center;
}
.customalert .customalert_footer {
  background: #fff;
  text-align: center;
}
.customalert.customalert_confirm .customalert_footer button.customalert_confirm {
  margin-right: 10px;
}
